<template><section><html><head></head><body><h2>DC-SDK Demo</h2>
<p>When vue-cesium uses dc-sdk to develop, you only need to specify the dc-sdk library address through the configuration item <code v-pre="">cesiumPath</code> when VueCesium is introduced.</p>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-title class_">Vue</span>.<span class="hljs-title function_">use</span>(<span class="hljs-title class_">VueCesium</span>, {
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'https://dc.dvgis.cn/libs/dc-sdk/dc.base.min.js'</span>
})
</code></pre>
<p>Or specify the address of cesiumPath as the dc-sdk path on the vc-viewer component. Such as the following example:</p>
<p><code v-pre="">vc-viewer</code> will return {Cesium, viewer, dcViewer} after loading successfully, and you can use the dc-sdk API to carry out related development through the <code v-pre="">dcViewer</code>, as shown in the following example:</p>
<h2>Example</h2>
<h3>Use VueCesium to load dc-sdk</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer" ref="viewerContainer">
      <vc-viewer ref="vcViewer" :cesium-path="cesiumPath" :animation="animation" :timeline="timeline" :fullscreen-button="fullscreenButton" :fullscreen-element="fullscreenElement" @ready="onViewerReady" @leftclick="onLeftClick">
        <vc-navigation></vc-navigation>
        <vc-entity ref="entity" @click="onEntityClick" :position="{lng: 108, lat: 32}" :point="point" :label="label">
          <vc-graphics-billboard ref="billboard" image="https://zouyaoji.top/vue-cesium/favicon.png"></vc-graphics-billboard>
          <vc-graphics-rectangle :coordinates="[130, 20, 80, 25]" material="green"></vc-graphics-rectangle>
        </vc-entity>
        <!-- 天地图注记 -->
        <vc-layer-imagery :sort-order="20">
          <vc-provider-imagery-tianditu map-style="cva_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
        <!-- 天地图影像 -->
        <vc-layer-imagery :sort-order="10">
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>Animation</span>
        <md-switch v-model="animation"></md-switch>
        <span>Timeline</span>
        <md-switch v-model="timeline"></md-switch>
        <span>BaseLayerPicker</span>
        <md-switch v-model="baseLayerPicker"></md-switch>
        <span>FullscreenButton</span>
        <md-switch v-model="fullscreenButton"></md-switch>
        <span>InfoBox</span>
        <md-switch v-model="infoBox"></md-switch>
        <md-button class="md-raised md-accent" @click="unload">Unload</md-button>
        <md-button class="md-raised md-accent" @click="load">Load</md-button>
        <md-button class="md-raised md-accent" @click="reload">Reload</md-button>
      </div>
    </div>
  </template>
  
</doc-preview>
